import { AcGameObject } from "./AcGameObject";
import { GameMap } from "./GameMap";
import { MultiGameMap } from "./MultiGameMap";
import { Player } from "./heros/Player";
import { MultiPlayerSocket } from "./socket/multiplayer";
import { NoticeBoard } from "./NoticeBoard";
import $ from 'jquery';
import Cookies from "js-cookie";
import { ChatField } from "./ChatField";
import { ScoreBoard } from "./ScoreBoard";
import router from "@/router";
import { FireMage } from "./heros/FireMage";
import { TaiEr } from "./heros/TaiEr";
import { XiaoYe } from "./heros/XiaoYe";
import { LongYan } from "./heros/LongYan";
import { DiShiTian } from "./heros/DiShiTian";

export class PlayGround extends AcGameObject {
    constructor(canvas, ctx, div, store, chat_field_ref) {
        super();
        this.canvas = canvas;
        this.ctx = ctx;
        this.div = div;
        this.store = store;
        this.width = this.div.clientWidth;
        this.height = this.div.clientHeight;
        this.chat_field_ref = chat_field_ref;

        this.scale = this.height;
        this.players = [];
        this.fireballs = [];
        this.virtual_map_width = 3;  // 
        this.virtual_map_height = this.virtual_map_width; // 
        // 
        this.ctx_x = 0.5 * this.width / this.scale;
        this.ctx_y = 0.5 * this.height / this.scale;

        this.game_map = new MultiGameMap(this, this.canvas);

    }

    start() {
        this.resize();
        let outer = this;
        let resize_uuid = this.create_uuid();
        $(window).on(`resize.${resize_uuid}`, function () {
            outer.resize();
            console.log("window.resize()");
        });

        if (this.store.state.AcWingOS !== "AcWingOS") {
            this.store.state.AcWingOS.api.window.on_close(function () {
                $(window).off(`resize.${resize_uuid}`);
                outer.hide();
            });
        }

        // let player = new Player(this, 0.5 * this.width / this.scale, 0.5 * this.height / this.scale, this.height * 0.05 / this.scale, "white", this.height * 0.3 / this.scale, "me");
        // this.players.push(player);
        // this.re_calculate_cx_cy(player.x, player.y);
        // this.focus_player = player;

        // for (let i = 0; i < 20; i++) {
        //     let rand_x = Math.random() * this.virtual_map_width;
        //     let rand_y = Math.random() * this.virtual_map_height;
        //     this.players.push(new Player(this, rand_x, rand_y, this.height * 0.05 / this.scale, this.get_random_color(), this.height * 0.3 / this.scale, "robot"));
        // }

    }

    quit() {
        this.show_select_mode();
    }

    restart(game_mode) {
        let outer = this;
        this.store.commit('updateScore', 0);
        this.store.commit('updateGameState', "waiting");

        let player = null;
        if (this.store.state.select_hero_name === "") {
            player = new TaiEr(this, 0.5 * this.width / this.scale, 0.5 * this.height / this.scale, this.height * 0.05 / this.scale, "white", this.height * 0.3 / this.scale, "me", this.store.state.username, this.store.state.photo);
        } else if (this.store.state.select_hero_name === "") {
            player = new XiaoYe(this, 0.5 * this.width / this.scale, 0.5 * this.height / this.scale, this.height * 0.05 / this.scale, "white", this.height * 0.3 / this.scale, "me", this.store.state.username, this.store.state.photo);
        } else if (this.store.state.select_hero_name === "") {
            player = new LongYan(this, 0.5 * this.width / this.scale, 0.5 * this.height / this.scale, this.height * 0.05 / this.scale, "white", this.height * 0.3 / this.scale, "me", this.store.state.username, this.store.state.photo);
        } else if (this.store.state.select_hero_name === "") {
            player = new DiShiTian(this, 0.5 * this.width / this.scale, 0.5 * this.height / this.scale, this.height * 0.05 / this.scale, "white", this.height * 0.3 / this.scale, "me", this.store.state.username, this.store.state.photo);
        } else {
            player = new TaiEr(this, 0.5 * this.width / this.scale, 0.5 * this.height / this.scale, this.height * 0.05 / this.scale, "white", this.height * 0.3 / this.scale, "me", this.store.state.username, this.store.state.photo);
        }
        this.players.push(player);
        this.re_calculate_cx_cy(player.x, player.y);
        this.focus_player = player;

        this.ctx.canvas.focus();

        if (game_mode === "single mode") {
            for (let i = 0; i < 20; i++) {
                let rand_x = Math.random() * this.virtual_map_width;
                let rand_y = Math.random() * this.virtual_map_height;
                this.players.push(new Player(this, rand_x, rand_y, this.height * 0.05 / this.scale, this.get_random_color(), this.height * 0.3 / this.scale, "robot"));
            }
            this.store.commit('updateGameState', "fighting");  // 
        } else if (game_mode === "multi mode") {
            this.notice_board = new NoticeBoard(this);
            this.chat_field = new ChatField(this, this.chat_field_ref);
            this.mps = new MultiPlayerSocket(this);  // 
            this.mps.uuid = this.players[0].uuid;  // uuiduuid

            // 
            this.mps.ws.onopen = function () {
                outer.mps.send_create_player(outer.store.state.username, outer.store.state.photo);
            };
        }
        this.score_board = new ScoreBoard(this);
    }

    create_uuid() {
        let res = "";
        for (let i = 0; i < 20; i++) {
            let x = parseInt(Math.floor(Math.random() * 10));
            res += x;
        }
        return res;
    }

    resize() {
        this.ctx.canvas.width = this.div.clientWidth;
        this.ctx.canvas.height = this.div.clientHeight;
        this.width = this.div.clientWidth;
        this.height = this.div.clientHeight;
        this.scale = this.height;
    }

    // 
    // 1this.scale
    my_calculate_relative_position_x(x) {
        return x - this.ctx_x + 0.5 * this.width / this.scale;
    }
    my_calculate_relative_position_y(y) {
        return y - this.ctx_y + 0.5 * this.height / this.scale;
    }

    // 
    my_calculate_tx(x) {
        return (x - 0.5 * this.width) / this.scale + this.ctx_x;
    }
    my_calculate_ty(y) {
        return (y - 0.5 * this.height) / this.scale + this.ctx_y;
    }


    // 
    re_calculate_cx_cy(x, y) {
        // 
        this.cx = x - 0.5 * this.width / this.scale;
        this.cy = y - 0.5 * this.height / this.scale;

        let cube_side_len = this.game_map.cube_side_len;
        if (this.focus_player) {
            this.cx = Math.max(this.cx, -2 * cube_side_len);
            this.cx = Math.min(this.cx, this.virtual_map_width - (this.width / this.scale - 2 * cube_side_len));
            this.cy = Math.max(this.cy, -cube_side_len);
            this.cy = Math.min(this.cy, this.virtual_map_height - (this.height / this.scale - cube_side_len));
        }
    }

    append_player() {
        let rand_x = Math.random() * this.virtual_map_width;
        let rand_y = Math.random() * this.virtual_map_height;
        this.players.push(new Player(this, rand_x, rand_y, this.height * 0.05 / this.scale, this.get_random_color(), this.height * 0.3 / this.scale, "robot"));

        const score = this.store.state.score + 1;
        this.store.commit('updateScore', score);
        this.store.commit('updateRecord', score);
        console.log("[score]:", this.store.state.score, "[record]:", this.store.state.record);
    }

    get_random_color() {
        let colors = ["red", "pink", "grey", "green", "lightblue", "lightgreen", "yellow", "orange", "gold"];
        return colors[Math.floor(Math.random() * colors.length)];
    }

    // 
    update_score() {
        console.log("update score: ", this.store.state.score);
        $.ajax({
            url: "https://app4689.acapp.acwing.com.cn:4436/update_score/",
            type: "post",
            data: {
                rank_score: this.store.state.score,
                game_mode: this.store.state.game_mode,
            },
            headers: {
                'Authorization': "Bearer " + Cookies.get("access"),
            },
        });
    }

    is_element_out_of_screen(ctx_x, ctx_y) {
        if (ctx_x < -0.2 * this.width / this.scale ||
            ctx_x > 1.2 * this.width / this.scale ||
            ctx_y < -0.2 * this.height / this.scale ||
            ctx_y > 1.2 * this.height / this.scale) {
            return true;
        }
        return false;
    }

    hide() {
        while (this.players && this.players.length > 0) {
            this.players[0].destroy();
        }

        while (this.fireballs && this.fireballs.length > 0) {
            this.fireballs[0].destroy();
        }

        if (this.game_map) {
            this.game_map.destroy();
            this.game_map = null;
        }

        if (this.notice_board) {
            this.notice_board.destroy();
            this.notice_board = null;
        }

        if (this.score_board) {
            this.score_board.destroy();
            this.score_board = null;
        }

        console.log("playground hide DONE");
    }

    show_select_mode() {
        this.hide();
        router.push("/select_mode");
        this.destroy();
    }

    update() {
        if (this.focus_player) {
            this.ctx_x = this.focus_player.x;
            this.ctx_y = this.focus_player.y;
        }

        this.render();
    }

    render() {
    }
}