import { Player } from "../heros/Player";
import { TaiEr } from "../heros/TaiEr";
import { XiaoYe } from "../heros/XiaoYe";
import { LongYan } from "../heros/LongYan";
import { DiShiTian } from "../heros/DiShiTian";

export class MultiPlayerSocket {
    constructor(playground) {
        this.playground = playground;

        this.ws = new WebSocket("wss://app4689.acapp.acwing.com.cn:4436/wss/multiplayer/");

        this.start();
    }

    start() {
        this.receive();
    }

    receive() {
        let outer = this;

        // wss
        this.ws.onmessage = function (e) {
            // json
            let data = JSON.parse(e.data);
            let uuid = data.uuid;
            // 
            if (uuid === outer.uuid) return false;

            let event = data.event;
            if (event === "create_player") {
                // wss
                outer.receive_create_player(uuid, data.username, data.photo);
            } else if (event === "move_toward") {
                outer.receive_move_toward(uuid, data.directions_array, data.x, data.y);
            } else if (event === "use_general_skill") {
                outer.receive_use_general_skill(uuid, data.skill_data);
            } else if (event === "use_awakened_skill") {
                outer.receive_use_awakened_skill(uuid, data.skill_data);
            } else if (event === "use_summoner_skill") {
                outer.receive_use_summoner_skill(uuid, data.skill_data);
            } else if (event === "fireball_attack") {
                outer.receive_fireball_attack(uuid, data.attackee_uuid, data.x, data.y, data.angle, data.damage, data.ball_uuid);
            } else if (event === "attack") {
                outer.receive_attack(uuid, data.attackee_uuid, data.x, data.y, data.angle, data.damage);
            } else if (event === "message") {
                outer.receive_chat_message(uuid, data.username, data.text);
            } else if (event === "update_player_info") {
                outer.receive_update_player_info(uuid, data.hero_name);
            } else if (event === "player_change_state") {
                outer.receive_player_change_state(uuid, data.attackee_uuid, data.data);
            }
        };
    }

    get_player(uuid) {
        let players = this.playground.players;
        for (let i = 0; i < players.length; i++) {
            let player = players[i];
            if (player.uuid === uuid) {
                return player;
            }
        }
        return null;
    }

    send_create_player(username, photo, hero_name) {
        let outer = this;
        this.ws.send(JSON.stringify({
            'event': "create_player",
            'uuid': outer.uuid,
            'username': username,
            'photo': photo,
            'hero_name': hero_name,
        }));
    }

    // 
    receive_create_player(uuid, username, photo) {
        let player = null;
        // let rand_x = Math.random() * this.playground.virtual_map_width;
        // let rand_y = Math.random() * this.playground.virtual_map_height;
        // player = new Player(this.playground, rand_x, rand_y, this.playground.height * 0.05 / this.playground.scale, "white", this.playground.height * 0.3 / this.playground.scale, "enemy", username, photo);
        let x = 0.5 * this.playground.width / this.playground.scale;
        let y = 0.5 * this.playground.height / this.playground.scale;
        player = new Player(this.playground, x, y, this.playground.height * 0.05 / this.playground.scale, "white", this.playground.height * 0.3 / this.playground.scale, "enemy", username, photo);

        // uuiduuid
        player.uuid = uuid;
        this.playground.players.push(player);

        this.send_update_player_info();
    }

    // 
    send_move_toward(directions, x, y) {
        let outer = this;
        // JSON.stringify()Set()
        // [...directions]Set()
        // new Set(array)
        let directions_array = [...directions];
        this.ws.send(JSON.stringify({
            'event': "move_toward",
            'uuid': outer.uuid,
            'directions_array': directions_array,
            'x': x,
            'y': y,
        }));
    }

    receive_move_toward(uuid, directions_array, x, y) {
        let player = this.get_player(uuid);

        if (player) {
            let directions = new Set(directions_array);
            player.x = x;
            player.y = y;
            player.update_speed_angle(directions);
        }
    }

    send_update_player_info() {
        let outer = this;
        this.ws.send(JSON.stringify({
            'event': "update_player_info",
            'uuid': outer.uuid,
            'hero_name': outer.playground.store.state.select_hero_name,
        }));
    }

    receive_update_player_info(uuid, hero_name) {
        let player = this.get_player(uuid);
        let username = player.username;
        let photo = player.photo;
        player.destroy();

        // let rand_x = Math.random() * this.playground.virtual_map_width;
        // let rand_y = Math.random() * this.playground.virtual_map_height;
        let rand_x = 0.5 * this.playground.width / this.playground.scale;
        let rand_y = 0.5 * this.playground.height / this.playground.scale;
        if (hero_name === "") {
            player = new TaiEr(this.playground, rand_x, rand_y, this.playground.height * 0.05 / this.playground.scale, "white", this.playground.height * 0.3 / this.playground.scale, "enemy", username, photo);
        } else if (hero_name === "") {
            player = new XiaoYe(this.playground, rand_x, rand_y, this.playground.height * 0.05 / this.playground.scale, "white", this.playground.height * 0.3 / this.playground.scale, "enemy", username, photo);
        } else if (hero_name === "") {
            player = new LongYan(this.playground, rand_x, rand_y, this.playground.height * 0.05 / this.playground.scale, "white", this.playground.height * 0.3 / this.playground.scale, "enemy", username, photo);
        } else if (hero_name === "") {
            player = new DiShiTian(this.playground, rand_x, rand_y, this.playground.height * 0.05 / this.playground.scale, "white", this.playground.height * 0.3 / this.playground.scale, "enemy", username, photo);
        } else {
            console.log("ERROR not a hero");
            // player = new TaiEr(this.playground, rand_x, rand_y, this.playground.height * 0.05 / this.playground.scale, "white", this.playground.height * 0.3 / this.playground.scale, "enemy", username, photo);
        }

        player.uuid = uuid;
        this.playground.players.push(player);
    }

    send_use_general_skill(skill_data) {
        let outer = this;
        this.ws.send(JSON.stringify({
            'event': "general_skill",
            'uuid': outer.uuid,
            'skill_data': skill_data,
        }));
    }

    receive_use_general_skill(uuid, skill_data) {
        let player = this.get_player(uuid);
        if (player) {
            player.general_skill.receive_use_skill(skill_data);
        }
    }

    send_use_awakened_skill(skill_data) {
        let outer = this;
        this.ws.send(JSON.stringify({
            'event': "awakened_skill",
            'uuid': outer.uuid,
            'skill_data': skill_data,
        }))
    }

    receive_use_awakened_skill(uuid, skill_data) {
        let player = this.get_player(uuid);
        if (player) {
            player.awakened_skill.receive_use_skill(skill_data);
        }
    }

    send_use_summoner_skill(skill_data) {
        let outer = this;
        this.ws.send(JSON.stringify({
            'event': "summoner_skill",
            'uuid': outer.uuid,
            'skill_data': skill_data,
        }));
    }

    receive_use_summoner_skill(uuid, skill_data) {
        let player = this.get_player(uuid);
        if (player) {
            player.summoner_skill.receive_use_skill(skill_data);
        }
    }

    // x, y: 
    // angle: 
    // ball_uuid: 
    send_attack(attackee_uuid, x, y, angle, damage) {
        let outer = this;
        this.ws.send(JSON.stringify({
            'event': "attack",
            'uuid': outer.uuid,
            'attackee_uuid': attackee_uuid,
            'x': x,
            'y': y,
            'angle': angle,
            'damage': damage,
        }));
    }

    receive_attack(uuid, attackee_uuid, x, y, angle, damage) {
        let attacker = this.get_player(uuid);
        let attackee = this.get_player(attackee_uuid);
        if (attacker && attackee) {
            attackee.receive_attack(x, y, angle, damage, attacker);
        }
    }

    send_fireball_attack(attackee_uuid, x, y, angle, damage, ball_uuid) {
        let outer = this;
        this.ws.send(JSON.stringify({
            'event': "fireball_attack",
            'uuid': outer.uuid,
            'attackee_uuid': attackee_uuid,
            'x': x,
            'y': y,
            'angle': angle,
            'damage': damage,
            'ball_uuid': ball_uuid,
        }));
    }

    receive_fireball_attack(uuid, attackee_uuid, x, y, angle, damage, ball_uuid) {
        let attacker = this.get_player(uuid);
        let attackee = this.get_player(attackee_uuid);
        if (attacker && attackee) {
            attackee.receive_fireball_attack(x, y, angle, damage, ball_uuid, attacker);
        }
    }

    receive_blink(uuid, tx, ty) {
        let player = this.get_player(uuid);
        if (player) {
            player.blink(tx, ty);
        }
    }

    send_chat_message(username, text) {
        let outer = this;
        this.ws.send(JSON.stringify({
            'event': "message",
            'uuid': outer.uuid,
            'username': username,
            'text': text,
        }));
    }

    receive_chat_message(uuid, username, text) {
        this.playground.chat_field.receive_add_message(username, text);
    }

    send_player_change_state(attackee_uuid, data) {
        let outer = this;
        this.ws.send(JSON.stringify({
            'event': "player_change_state",
            'uuid': outer.uuid,
            'attackee_uuid': attackee_uuid,
            'data': data,
        }));
    }

    receive_player_change_state(uuid, attackee_uuid, data) {
        let attacker = this.get_player(uuid);
        let attackee = this.get_player(attackee_uuid);
        if (attacker && attackee) {
            attackee.change_state(data);
        }
    }
}