import { isMemoSame, readonly } from "vue";
import { routeLocationKey } from "vue-router";
import { AcGameObject } from "../AcGameObject";
import { Particle } from "../Particle";
import { FireBall } from "../skill/FireBall";
import { HealthBar } from "../player_component/HealthBar";
import { FireBallSkill } from "../skill/FireBallSkill";
import { BlinkSkill } from "../skill/BlinkSkill";
import { ShieldSkill } from "../skill/ShieldSkill";
import heros_info from "../../../../../static/HeroInfo.json"
import { Arrow } from "../player_component/Arrow";
import { timePanelSharedProps } from "element-plus/es/components/time-picker/src/props/shared";

export class Player extends AcGameObject {
    constructor(playground, x, y, radius, color, speed, character, username, photo) {
        super();
        this.playground = playground;
        this.ctx = this.playground.ctx;
        this.x = x;
        this.y = y;
        this.vx = 0;
        this.vy = 0;
        this.base_radis = radius;
        this.radius = this.base_radis;
        this.color = color;
        this.base_speed = speed;
        this.speed = 0;
        this.speed_angle = 0;  // 
        this.character = character;
        this.username = username;
        this.photo = photo;
        this.decelerate_ratio = 0;  // 

        this.hero_name = "Player";
        this.base_health = 100;
        this.health = this.base_health;
        this.level = 1;  // 
        // 
        // normal  displacement  vertigo  silent  poisoning  transformation
        //                                 
        this.state = "normal";
        this.eps = 0.001;
        this.directions = new Set();  // 
        this.last_directions_size = 0;  // directions
        this.skill_directions = new Set();  // 
        this.last_skill_directions_size = 0;
        this.cur_skill = null;
        this.rand_directions = new Set();  // 
        this.tx = 0;  // 
        this.ty = 0;
        this.friction = 0.9;  // 
        this.spent_time = 0;
        this.last_clientX = 0;  // e.clientXe.clientY
        this.last_clientY = 0;
        this.last_rect_left = 0;
        this.last_rect_top = 0;  // rect.leftrect.topAcWingOS

        this.general_skill = new FireBallSkill(this.playground, this, 0.6, 0.9, 0.04);  // 
        this.awakened_skill = new ShieldSkill(this.playground, this, 0.8, 0.9, 0.04);  // 
        this.summoner_skill = new BlinkSkill(this.playground, this, 1.0, 0.9, 0.04);  // 
        this.health_bar = new HealthBar(this.playground, this);
        if (this.character === "me") {
            this.arrow = new Arrow(this.playground, this, 1);
        }
    }

    start() {
        if (this.playground.store.state.game_mode === "multi mode") {
            this.playground.notice_board.add();
            if (this.playground.notice_board.player_count >= 3) {
                this.playground.store.commit('updateGameState', "fighting");
                this.playground.notice_board.write("Fighting");
            }
        }

        this.ctx.canvas.focus();

        if (this.character !== "robot") {
            this.load_image();
        }

        // 
        if (this.character === "me") {
            this.add_listening_events();
            console.log("Hero Name:", this.hero_name);
        } else if (this.character === "robot") {
            this.move_length = 0;
            this.speed = this.get_speed();
            this.robot_update();

            // 
            // for (let i = 0; i < 4; i++) {
            //     // Math.round(Math.random())01
            //     this.rand_directions.add(Math.round(Math.random() * 4));
            // }
        }
    }

    level_up() {
        this.level += 1;
        this.health = this.base_health;
    }

    change_state(data) {
        if (data["skill_name"] === "transformation") {
            this.transformation(data["transformation_radis_ratio"]);
        } else if (data["skill_name"] === "transformation_restore") {
            this.transformation_restore();
        } else if (data["skill_name"] === "ice_arrow") {
            this.decelerate(data["decelerate_ratio"], data["time"]);

            console.log("DECELERATE:", this.decelerate_ratio);
        }
    }

    get_speed() {
        return this.base_speed * (1 - this.decelerate_ratio);
    }

    change_decelerate_ratio(decelerate_ratio, info) {
        // info
        if (info && info === "add") {
            this.decelerate_ratio += decelerate_ratio;
        } else {
            this.decelerate_ratio = decelerate_ratio;
        }
        if (this.speed > this.eps) {
            this.speed = this.get_speed();
        }
    }

    // 
    decelerate(decelerate_ratio, time) {
        let outer = this;
        if (this.decelerate_id) {
            clearTimeout(this.decelerate_id);
        }

        if (this.decelerate_ratio < 1) {
            this.change_decelerate_ratio(decelerate_ratio, "add");
        }
        if (this.decelerate_ratio > 1) {
            this.decelerate_ratio = 1;
        }

        console.log("time:", time * 1000);

        this.decelerate_id = setTimeout(function () {
            outer.change_decelerate_ratio(0);
            outer.decelerate_id = null;
            // outer.speed = outer.get_speed();
            console.log("restore speed");
        }, time * 1000);
    }

    // 
    transformation(transformation_radis_ratios) {
        this.state = "transformation";
        this.radius = this.base_radis * transformation_radis_ratios;
    }

    // 
    transformation_restore() {
        this.state = "normal";
        this.radius = this.base_radis;
        this.change_decelerate_ratio(0);
    }

    load_image() {
        this.img = new Image();
        for (let hero_info of heros_info) {
            if (hero_info.name === this.hero_name) {
                this.img.src = hero_info.avatar;
                break;
            }
        }
        if (!this.img.src) {
            this.img.src = this.photo;
        }
    }

    add_listening_events() {
        // 
        this.ctx.canvas.addEventListener('contextmenu', e => {
            e.preventDefault();  // 
        });

        this.ctx.canvas.addEventListener('keydown', e => {
            // console.log("in [player]", e.key);
            if (e.key === 'Enter') {
                if (this.playground.store.state.game_mode === "multi mode") {
                    this.playground.chat_field.show_input();
                }
                e.preventDefault();
            } else if (e.key === 'Escape') {
                if (this.playground.store.state.game_mode === "multi mode") {
                    this.playground.chat_field.hide_input();
                }
                e.preventDefault();
            }

            // 
            if (this.playground.store.state.game_state !== "fighting") {
                return true;
            }
            // wasd / 
            if (e.key === 'w' || e.key === 'W' || e.key === 'ArrowUp') {
                // delete
                this.directions.add(0);
                e.preventDefault();  // 
            } else if (e.key === 'd' || e.key === 'D' || e.key === 'ArrowRight') {
                this.directions.add(1);
                e.preventDefault();  // 
            } else if (e.key === 's' || e.key === 'S' || e.key === 'ArrowDown') {
                this.directions.add(2);
                e.preventDefault();  // 
            } else if (e.key === 'a' || e.key === 'A' || e.key === 'ArrowLeft') {
                this.directions.add(3);
                e.preventDefault();  // 
            } else if (e.key === 'f' || e.key === 'F') {
                this.cur_skill = this.cur_skill === "summoner_skill" ? null : "summoner_skill";
            } else if (e.key === ' ') {
                this.awakened_skill.use_skill(this.tx, this.ty);
            }

            this.update_move_toward();
        });

        this.ctx.canvas.addEventListener('keyup', e => {
            if (e.key === 'w' || e.key === 'W' || e.key === 'ArrowUp') {
                this.directions.delete(0);
                e.preventDefault();  // 
            } else if (e.key === 'd' || e.key === 'D' || e.key === 'ArrowRight') {
                this.directions.delete(1);
                e.preventDefault();  // 
            } else if (e.key === 's' || e.key === 'S' || e.key === 'ArrowDown') {
                this.directions.delete(2);
                e.preventDefault();  // 
            } else if (e.key === 'a' || e.key === 'A' || e.key === 'ArrowLeft') {
                this.directions.delete(3);
                e.preventDefault();  // 
            }
            this.update_move_toward();
        });

        this.ctx.canvas.addEventListener('mousedown', e => {
            // 
            if (this.playground.store.state.game_state !== "fighting") {
                return false;
            }
            this.save_clientX_clientY_rectLeft_rectRight(e);
            // 
            if (e.which === 1) {
                this.my_calculate_tx_ty();
                if (this.cur_skill === null) {
                    this.use_general_skill();
                    this.skill_directions.add("general_skill");
                } else if (this.cur_skill === "summoner_skill") {
                    this.use_summoner_skill();
                    this.cur_skill = null;
                }
                // 
                // if (this.tx < 0 || this.tx > this.playground.virtual_map_width || this.ty < 0 || this.ty > this.playground.virtual_map_height) {
                //     return ;
                // }
            }
            e.preventDefault();
        });

        this.ctx.canvas.addEventListener('mouseup', e => {
            this.save_clientX_clientY_rectLeft_rectRight(e);
            this.skill_directions.delete("general_skill");
            e.preventDefault();
        })

        // 
        // bug
        this.ctx.canvas.addEventListener('mousemove', e => {
            this.save_clientX_clientY_rectLeft_rectRight(e);
        })
    }

    use_general_skill() {
        // 
        if (this.state === "transformation" || this.state === "silent" || this.state === "vertigo") {
            return;
        }
        this.general_skill.use_skill(this.tx, this.ty);
    }

    use_summoner_skill() {
        // 
        if (this.state === "transformation" || this.state === "silent" || this.state === "vertigo") {
            return;
        }
        this.summoner_skill.use_skill(this.tx, this.ty);
    }

    // uuid
    destroy_fireball(uuid) {
        for (let i = 0; i < this.playground.fireballs.length; i++) {
            let fireball = this.playground.fireballs[i];
            if (fireball.uuid === uuid) {
                fireball.destroy();
                break;
            }
        }
    }

    scan_skills(directions) {
        if (directions.has("general_skill")) {
            this.use_general_skill();
        }
    }

    // 
    save_clientX_clientY_rectLeft_rectRight(e) {
        this.last_clientX = e.clientX;
        this.last_clientY = e.clientY;
    }

    // 
    my_calculate_tx_ty() {
        // canvasacappWEBcanvas
        const rect = this.ctx.canvas.getBoundingClientRect();
        this.last_rect_left = rect.left;
        this.last_rect_top = rect.top;
        this.tx = this.playground.my_calculate_tx(this.last_clientX - this.last_rect_left);
        this.ty = this.playground.my_calculate_ty(this.last_clientY - this.last_rect_top);
    }

    // 
    get_dist(x1, y1, x2, y2) {
        let dx = x1 - x2;
        let dy = y1 - y2;
        return Math.sqrt(dx * dx + dy * dy);
    }

    update_speed_angle(directions) {
        // 
        this.speed = this.get_speed();
        if (directions.size === 1 || directions.size === 3) {
            if (directions.has(0) && !directions.has(2))
                this.speed_angle = -Math.PI / 2;
            else if (directions.has(1) && !directions.has(3))
                this.speed_angle = 0;
            else if (directions.has(2) && !directions.has(0))
                this.speed_angle = Math.PI / 2;
            else if (directions.has(3) && !directions.has(1))
                this.speed_angle = Math.PI;
        } else if (directions.size === 2) {
            if (directions.has(0) && directions.has(1))
                this.speed_angle = -Math.PI / 4;
            else if (directions.has(1) && directions.has(2))
                this.speed_angle = Math.PI / 4;
            else if (directions.has(2) && directions.has(3))
                this.speed_angle = Math.PI * 3 / 4;
            else if (directions.has(3) && directions.has(0))
                this.speed_angle = -Math.PI * 3 / 4;
        } else {
            this.speed = 0;
        }
    }

    // 
    is_attacked(angle, damage) {
        for (let i = 0; i < 15 + Math.random() * 10; i++) {
            let radius = this.radius * Math.random() * 0.1;
            let angle = Math.PI * 2 * Math.random();
            let vx = Math.cos(angle), vy = Math.sin(angle);
            let color = this.color;
            let speed = this.speed * 4;  // 
            let move_length = this.radius * Math.random() * 5;  // 
            new Particle(this.playground, this.x, this.y, radius, vx, vy, color, speed, move_length);
        }

        this.health -= damage;
        if (this.health <= 0) {
            this.player_lose();
            return true;
        }

        return false;
    }

    // 
    receive_attack(x, y, angle, damage, attacker) {
        this.x = x;
        this.y = y;
        this.is_attacked(angle, damage);
    }

    receive_fireball_attack(x, y, angle, damage, ball_uuid, attacker) {
        attacker.destroy_fireball(ball_uuid);
        this.x = x;
        this.y = y;
        this.is_attacked(angle, damage);
    }

    player_lose() {
        // 
        if (this.character === "me") {
            // 
            if (this.playground.store.state.game_state === "fighting") {
                console.log("me LOST");
                this.playground.score_board.lose();
            }
        } else if (this.character === "robot") {
            this.playground.append_player();
        }

        this.destroy();
    }

    on_destroy() {
        if (this.health_bar) {
            this.health_bar.destroy();
        }
        this.health_bar = null;

        if (this.general_skill) {
            this.general_skill.destroy();
        }
        this.general_skill = null;

        if (this.awakened_skill) {
            this.awakened_skill.destroy();
        }
        this.awakened_skill = null;

        if (this.summoner_skill) {
            this.summoner_skill.destroy();
        }
        this.summoner_skill = null;

        for (let i = 0; i < this.playground.players.length; i++) {
            if (this.playground.players[i] === this) {
                this.playground.players.splice(i, 1);
                break;
            }
        }
    }

    // directionsoperationSet()
    // from_directions_clean(directions, operation) {
    //     for (let i = 0; i < directions.length; i++) {
    //         if (directions[i] === operation) {
    //             directions.splice(i, 1);
    //             i--;
    //         }
    //     }
    // }

    robot_update() {
        // 
        if (this.state !== "silent" && this.state !== "transformation") {
            this.auto_use_general_skill();
        }

        // 
        if (this.state !== "displacement" && this.state !== "vertigo") {
            if (this.move_length < this.eps) {
                this.move_length = 0;
                this.vx = this.vy = 0;
                // 
                let tx = Math.random() * this.playground.virtual_map_width;
                let ty = Math.random() * this.playground.virtual_map_height;
                this.move_to(tx, ty);
            } else {
                let moved = Math.min(this.move_length, this.speed * this.timedelta / 1000);
                this.x += this.vx * moved;
                this.y += this.vy * moved;
                this.move_length -= moved;
            }
        }

    }

    // 
    auto_use_general_skill() {
        let players = this.playground.players;

        if (this.spent_time > 3 && Math.random() < 1 / 180.0 && players.length >= 2) {
            let player = this;
            for (let i = 0; player === this && i < 1000; i++) {
                player = players[Math.floor(Math.random() * players.length)];
            }
            this.general_skill.use_skill(player.x, player.y);
        }
    }

    move_to(tx, ty) {
        this.move_length = this.get_dist(this.x, this.y, tx, ty);
        this.speed_angle = Math.atan2(ty - this.y, tx - this.x);
        // this.check_collision();
        this.vx = Math.cos(this.speed_angle);
        // robotYplayerplayer
        // Math.PI / 2vy
        // this.move_lengthplayerthis.update_move();
        this.vy = Math.sin(this.speed_angle);
    }

    update_move() {
        // 
        if (this.state === "displacement" || this.state === "vertigo") {
            return false;
        }
        this.vx = this.speed * Math.cos(this.speed_angle);
        this.vy = this.speed * Math.sin(this.speed_angle);  // 
        this.x += this.vx * this.timedelta / 1000;
        this.y += this.vy * this.timedelta / 1000;
    }

    check_collision() {
        let grids = this.playground.game_map.grids;
        for (let i = 0; i < grids.length; i++) {
            if (grids[i].character === "wall") {
                let new_angle = grids[i].is_collision(this);
                if (new_angle) {
                    // this.speed_angle = new_angle;
                }
            }
        }
    }

    update_move_toward() {
        // 
        // 
        if (this.last_directions_size !== this.directions.size) {
            this.update_speed_angle(this.directions);
            if (this.playground.store.state.game_mode === "multi mode") {
                // *************************************************************************************
                // 
                // mps
                // mpsmultiplayer.jssend_move_toward
                // 'event'
                // async def receive(self, text_data)move_toward
                // move_towardasync def group_send_event(self, data)
                // multiplayer.jsreceive()this.ws.onmessage
                // *************************************************************************************
                this.playground.mps.send_move_toward(this.directions, this.x, this.y);
            }

            this.last_directions_size = this.directions.size;
        }
    }

    update() {

    }

    late_update() {
        this.spent_time += this.timedelta / 1000;

        if (this.character === "robot") {
            if (this.playground.store.state.game_state === "fighting") {
                this.robot_update();
            }
            this.render();
        }
    }

    late_late_update() {
        if (this.character === "robot") {
            return;
        }

        this.my_calculate_tx_ty();
        if (this.playground.store.state.game_state === "fighting") {
            // 
            if (this.character === "me") {
                this.update_win();
            }
            this.update_move();
            this.update_attack();
        }
        this.render();
    }

    update_win() {
        if (this.playground.players.length === 1) {
            this.playground.score_board.win();
        }
    }

    update_attack() {
        this.scan_skills(this.skill_directions);
    }

    render() {
        // canvas
        let scale = this.playground.scale;
        let ctx_x = this.playground.my_calculate_relative_position_x(this.x);
        let ctx_y = this.playground.my_calculate_relative_position_y(this.y);

        if (this.playground.is_element_out_of_screen(ctx_x, ctx_y)) {
            if (this.character != "me") { // bugreturn
                return;
            }
        }

        if (this.character !== "robot") {
            this.ctx.save();
            this.ctx.beginPath();
            this.ctx.arc(ctx_x * scale, ctx_y * scale, this.radius * scale, 0, Math.PI * 2, false);
            this.ctx.stroke();
            this.ctx.clip();
            this.ctx.drawImage(this.img, (ctx_x - this.radius) * scale, (ctx_y - this.radius) * scale, this.radius * 2 * scale, this.radius * 2 * scale);
            this.ctx.restore();
        } else {
            this.ctx.beginPath();
            this.ctx.arc(ctx_x * scale, ctx_y * scale, this.radius * scale, 0, Math.PI * 2, false);
            this.ctx.fillStyle = this.color;
            this.ctx.fill();
        }
    }
}